import { writable, derived } from 'svelte/store';

const DEFAULT = ['ETH', 'SOL', 'XBT', 'MATIC', 'ZEC', 'BAT', 'SAND'];

// stores
const locallyStored = JSON.parse(localStorage.assets || '""')
export const assets = writable(locallyStored || DEFAULT)
assets.subscribe((value) => localStorage.assets = JSON.stringify(value))

function createApiData() {
	const { subscribe, set, update } = writable({});

    return {
		subscribe,
		reset: () => set({}),
		add: (asset, data) => update(s => { s[asset] = data; return s }),
	};
}
export const apiData = createApiData();

//export const apiData = writable(JSON.parse(localStorage.data))
//apiData.subscribe((value) => {})

export const assetPrices = derived(apiData, ($apiData) => {
  const result = {};
  for (const [key, value] of Object.entries($apiData)) {
    if (key == 'last') { continue; }
    result[key] = value[value.length-1].y
  }
  return result;
});

export const chartData = derived(apiData, ($apiData) => {
	const xKey = 'x';
	const yKey = 'y';
	const zKey = 'name';

  const FACTOR = {
		XBT: 0.1,
		SOL: 30,
		ZEC: 20,
		SAND: 1000,
		MATIC: 2000,
		BAT: 4000,
	}

  let stats = [];
  for (const [key, value] of Object.entries($apiData)) {
    const median = value.map(d => d.y).sort((a, b) => a - b)[value.length / 2];
    stats.push({
      [zKey]: key,
      median: median,
    });
  }
  stats.sort((a, b) => b.median - a.median)
  
  const data = stats.map((s, i) => {
    const key = s[zKey];
    const factor = FACTOR[key] || ((stats[1].median || 1.0) / s.median);
    return {
      [zKey]: s[zKey],
      values: $apiData[key].map(d => ({
        x: new Date(d.x * 1000),
        y: d.y * factor * (factor > 1 ? (1 - i / stats.length) : 1),
      })),
    }
  });

  return data;
});
