<script>
	import { onMount } from "svelte";
	import { assets, apiData, assetPrices, chartData } from './store.js';

	import { LayerCake, Svg, Html } from 'layercake';
	import { scaleOrdinal } from 'd3-scale';
	import { timeParse, timeFormat } from 'd3-time-format';
	import { format, precisionFixed } from 'd3-format';

	import MultiLine from './components/MultiLine.svelte';
	import AxisX from './components/AxisX.svelte';
	import AxisY from './components/AxisY.svelte';
	import Labels from './components/GroupLabels.html.svelte';
	import SharedTooltip from './components/SharedTooltip.html.svelte';

	export let title;

	onMount(async () => {
		$assets.map(asset => {
			fetch("https://myapis-1470585854240.appspot.com/?url=https%3A%2F%2Fapi.kraken.com%2F0%2Fpublic%2FOHLC%3Finterval%3D60%26pair%3D"+asset+"USD")
			.then(response => response.json())
			.then(data => {
				// console.log(data);
				if (data.result) {
					const result = data.result;
					delete result.last
					// OHCL 0=time, 1=open, ..., 4=close
					apiData.add(asset, Object.values(result)[0].map(
						d => ({
							x:d[0],
							y:d[4],
						})
					));
				}
			}).catch(error => {
				console.log(error);
				return [];
			});
		});
	});

	const xKey = 'x';
	const yKey = 'y';
	const zKey = 'name';
	const seriesNames = $assets;
	const seriesColors = ['#9e0142', '#d53e4f', '#f46d43', '#fdae61', '#fee08b', '#e6f598', '#abdda4', '#66c2a5', '#3288bd', '#5e4fa2'].reverse();

	const flatten = data => data.reduce((memo, group) => {
		return memo.concat(group.values);
	}, []);

	const formatTickX = timeFormat('%b. %e');
	const formatTickY = d => format(`.${precisionFixed(d)}s`)(d);
</script>

<main>
	<h1>{title} {$assets.slice(0, 3).join(', ')}, ...</h1>

	{#each $assets as asset}
		{@const price = $assetPrices[asset]}
		<h2>{asset}: {price ? '$' + Number(price).toLocaleString() : ''}</h2>
	{/each}

	{#if Object.keys($apiData).length === $assets.length}
	<div class="chart">
	<LayerCake
		x={xKey}
		y={yKey}
		z={zKey}
		yDomain={[0, null]}
		zScale={scaleOrdinal()}
		zDomain={seriesNames}
		zRange={seriesColors}
    	flatData={flatten($chartData)}
		data={$chartData}
	>
		<Svg>
		<AxisX
			gridlines={false}
			ticks={$chartData[0].values.map(d => d[xKey]).sort((a, b) => a - b).filter( (value, index, ar) => ([0, 179, 359, 539, 719].includes(index)) )}
			formatTick={formatTickX}
			snapTicks={true}
			tickMarks={true}
		/>
		<AxisY
			ticks={4}
			formatTick={formatTickY}
		/>
		<MultiLine/>
		</Svg>

		<Html>
		<Labels/>
		<SharedTooltip
			formatTitle={formatTickX}
			dataset={$chartData}
		/>
		</Html>
	</LayerCake>
	</div>
	{/if}
</main>

<style>
	main {
		padding: 1em;
		margin: 0 0;
		font-family: sans-serif;
		font-size: 75%;
	}

	h1 {
		margin-top: 0;
	}

	.chart {
		width: 100%;
		height: 400px;
	}
</style>
